/**
 * 页面模块的默认数据
 */

/**
 * 页面基础信息
 */
exports.defaultPageData = {
  title: "",
  desc: "",
  backgroundColor: "",
  backgroundImage: ""
}
/**
 * 图片模块基础信息
 */
exports.imgFactory =()=> {
  return {
    type: "img",
    id:"block_"+new Date().getTime(),
    backgroundColor: "",
    backgroundImage: "",
    height:200,
    ranges: [],
  }
}
/**
 * 默认矩形的信息
 */
exports.rangeFactory = () => {
  return {
    id:"range_"+new Date().getTime(),
    x: 355,
    y: 100,
    w: 100,
    h: 50,
    sku: ""
  }
}
/**
 * 产品列表基础信息
 */
exports.productFactory =()=> {
  return {
    type:"product",
    id:"block_"+new Date().getTime(),
    backgroundColor:"",
    listChannel:"10",
    listAttr:["goodsShowName","goodsShowDesc","image","salePrice","marketPrice"],
    list:[],
    styles:{},
    itemStyles:{},
    itemOffset:{
      x:0,y:0
    }
  }
}
exports.productItemFactory=()=>{
  return {
    sku:"",
    title:"",

  }
}



