<template>
    <div @click.stop>
        <el-tabs class="bg" value="base" @tab-click="handleClick">
            <el-tab-pane name="base">
                <span slot="label" class="tab-title">图片模块</span>
                <el-form ref="form" :model="blockData" class="form-table">
                    <el-form-item label="添加热区">
                        <el-button @click="addRange" type="primary" size="mini">添加</el-button>
                    </el-form-item>
                    <el-form-item label="背景色">
                        <el-color-picker @change="updateColor" v-model="blockData.backgroundColor"></el-color-picker>
                    </el-form-item>
                    <el-form-item label="背景图片">
                        <el-upload ref="uploader" :on-success="uploaded" action="/api/imgs/upload" list-type="picture">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                     <el-form-item label="背景图片">
                         <el-button @click="delImg" size="small" type="primary">删除背景图片</el-button>
                    </el-form-item>
                    <el-form-item label="高度">
                        <el-input @blur="updateHeight" v-model="blockData.height" size="small"></el-input>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
        </el-tabs>
        <div class="bg" v-if="currentItem">
            <div class="title">编辑热点</div>
            <el-form :model="currentItem" class="form-table" label-width="65px">
                <el-form-item label="链接sku">
                    <el-input @blur="submitData" v-model="currentItem.sku" size="small"></el-input>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
    import imgEvent from "../components/eventSolt";
    import {
        imgFactory
    } from "../common/defaultData";
   
    export default {
        props: ["block","id"],
        data() {
            return {
                blockData: Object.assign(imgFactory(), this.block),
                currentItem: null
            };
        },
        mounted() {
            imgEvent.$on("current", (eid,val) => {
                if(eid!=this.id)return;
                this.currentItem = val;
            });
            this.currentItem = imgEvent.imgData;
        },
        methods: {
            handleClick(tab, event) {},
            updateColor(val) {
                this.blockData.backgroundColor = val;
                imgEvent.$emit('updateBase', this.id,this.blockData);
            },
            uploaded(response, file) {
                if (response.code == 1) {
                    this.blockData.backgroundImage = response.data;
                    imgEvent.$emit('updateBase',this.id, this.blockData);
                    setTimeout(() => {
                        this.$refs.uploader.clearFiles();
                    }, 1000);
                }
            },
            delImg(){
                this.blockData.backgroundImage = '';
                    imgEvent.$emit('updateBase',this.id, this.blockData);
            },
            updateHeight() {
                imgEvent.$emit('updateBase',this.id, this.blockData);
            },
            addRange() {
                imgEvent.$emit('addRange',this.id);
            },
            submitData() {
                imgEvent.$emit('updateRange',this.id, this.currentItem);
            }
        }
    };
</script>

<style scoped>
    .bg {
        background: #fff;
        margin-bottom: 10px;
        box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.8);
    }
    .title {
        padding: 5px;
        border-bottom: solid 1px #ccc;
    }
    .tab-title {
        padding: 0 5px;
        font-size: 12px;
    }
    .form-table {
        padding: 5px 10px;
    }
    .el-form-item{
        margin-bottom: 1px;
    }
</style>



