<template>
    <div class="bg">
        <div class="title">
            页面设置
        </div>
        <el-tabs value="base" @tab-click="handleClick">
            <el-tab-pane name="base">
                <span slot="label" class="tab-title">基本信息</span>
                <el-form ref="form" :model="pageData" class="form-table">
                    <el-form-item label="">
                        <el-input @blur="updateData" v-model="pageData.title" placeholder="请填写标题"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input @blur="updateData" v-model="pageData.desc" placeholder="请填写说明"></el-input>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane name="bg">
                <span slot="label" class="tab-title">背景信息</span>
                <el-form ref="form" :model="pageData" class="form-table">
                    <el-form-item label="背景色">
                        <el-color-picker @change="updateData" v-model="pageData.backgroundColor"></el-color-picker>
                    </el-form-item>
                    <el-form-item label="背景图片">
                        <el-upload :on-success="uploaded" action="https://jsonplaceholder.typicode.com/posts/" :limit="1" list-type="picture">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>


<script>
import { defaultPageData } from "../common/defaultData";
export default {
  props: ["data"],
  data() {
    return {
      activeName: "图片类型",
      pageData: Object.assign(defaultPageData, this.data)
    };
  },
  mounted() {},
  methods: {
    handleClick(tab, event) {},
    uploaded(response, file) {
        console.log(file);
        this.pageData.backgroundImage="";
        this.$emit("change",this.pageData);
    },
    updateData(){
        console.log(this.pageData)
        this.$emit("change",this.pageData);
    }
  }
};
</script>

<style scoped>
.bg{
    background: #fff;
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.8);
}
.title {
  padding: 5px;
  border-bottom: solid 1px #ccc;
}
.tab-title {
  padding: 0 5px;
  font-size: 12px;
}
.form-table {
  padding: 0 10px;
}
</style>



