import Vue from 'vue'
import Router from 'vue-router'
/**
 * 页面
 */
import index from '@/pages/index'
import item from '@/pages/item'
import login from '@/pages/login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/item/:id',
      name: 'item',
      component: item
    },
    {
      path: '/login',
      name: 'login',
      component: login
    }
  ]
})



