<template>
  <el-container>
    <!--[头部]-->
    <el-header class="header" height="50px">
      <div class="item">
        <el-button @click="back" class="no-btn" type="primary" icon="el-icon-arrow-left" style="padding:12px 15px;"></el-button>
      </div>
      <div class="item">
        <el-dropdown @command="addMoudle" placement="bottom-start">
          <el-button type="primary" class="no-btn" icon="el-icon-plus">
            添加模块<i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="add_img">图片模块</el-dropdown-item>
            <el-dropdown-item command="add_pro">产品列表模块</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="item">
        <!-- <el-button @click="seePrew" class="no-btn" icon="el-icon-caret-right" type="primary">预览</el-button> -->
      </div>
    </el-header>
    <div @click="cancelSelect">
      <el-container class="wrap">
        <!--[中间]-->
        <div>
          <div id="main">
            <div :ref="'block-'+index" @click.stop="selectMoudle(index,item)" v-for="(item,index) of blocks" :key="index">
              <div @change="updateBlock" :selected="currentIndex==index" :is="item.type+'-module'" :id="item.id" :block="item"></div>
            </div>
          </div>
        </div>
        <div id="setting">
          <div class="content" v-if="currentData" :style="{top:settingTop}">
            <!-- <div class="item">
              <el-button @click.stop="movePre" icon="el-icon-caret-top"></el-button>
            </div>
            <div class="item">
              <el-button @click.stop="moveNext" icon="el-icon-caret-bottom"></el-button>
            </div> -->
            <div class="item">
              <el-button @click.stop="del" type="danger" icon="el-icon-close"></el-button>
            </div>
          </div>
        </div>
        <div>
          <div id="editor">
            <page-editor @change="submitData" :data.sync="page" v-if="!currentData" />
            <div v-else >
                <div v-for="(item,index) of blocks" :key="index">
                  <div @change="submitData" v-show="index==currentIndex" :is="item.type+'-editor'" :id="item.id" :block="item"></div>
                </div>
            </div>
          </div>
        </div>
      </el-container>
    </div>
  </el-container>
</template>

<script>
import pageEditor from "../components/pageEditor.vue";
import imgMoudle from "../components/imgMoudle.vue";
import imgEditor from "../components/imgEditor.vue";
import productModule from "../components/productModule.vue";
import productEditor from "../components/productEditor.vue";
import {
  defaultPageData,
  imgFactory,
  productFactory
} from "../common/defaultData";
import request from "../common/request";

export default {
  components: {
    "page-editor": pageEditor,
    "img-module": imgMoudle,
    "img-editor": imgEditor,
    "product-module": productModule,
    "product-editor": productEditor
  },
  data() {
    return {
      id: 0,
      currentIndex: -1,
      currentData: null,
      settingTop: "0px",
      page: defaultPageData,
      blocks: []
    };
  },
  async mounted() {
    this.id = this.$route.params.id;
    if (!this.id) {
      this.$router.push("/");
      return;
    }
    this.refresh();
  },
  methods: {
    async refresh() {
      let resData = await request.getJson("/api/pages/get/" + this.id);
      try {
        let presetData = JSON.parse(resData.data.preset);
        if (presetData) {
          this.page.title = presetData.page.title;
          this.page.desc = presetData.page.desc;
          this.page.backgroundColor = presetData.page.backgroundColor;
          this.page.backgroundImage = presetData.page.backgroundImage;
          this.blocks = presetData.blocks;
        }
      } catch (e) {
        console.log(e);
      }
    },
    /**
       * 返回首页
       */
    back() {
      this.$router.push("/");
    },
    //预览
    seePrew() {},
    /**
       * 取消选中模块
       */
    cancelSelect() {
      this.currentIndex = -1;
      this.currentData = null;
    },
    addMoudle(val) {
      if (this[val]) {
        this[val]();
      }
    },
    //添加模块
    add_img() {
      this.blocks.push(imgFactory());
    },
    add_pro() {
      this.blocks.push(productFactory());
    },
    updateBlock(val) {
      this.submitData();
    },
    /**
       * 选中模块
       */
    selectMoudle(index, item) {
      this.currentIndex = index;
      this.currentData = item;
      let top = 0;
      for (let i = 0; i < index; i++) {
        top += this.$refs["block-" + i][0].offsetHeight;
      }
      top += this.$refs["block-" + index][0].offsetHeight / 2 - 46;
      if (top < 0) top = 0;
      this.settingTop = top + "px";
    },
    async movePre() {
      // let list = this.blocks.filter((item, index) => {
      //   if (index !== this.currentIndex) return item;
      // });
      // let item = this.blocks[this.currentIndex]
      // list.splice(this.currentIndex - 1, 0, item);
      // this.blocks = list;
      let list = this.blocks.splice(this.currentIndex, 1);
      setTimeout(() => {
        this.blocks.splice(this.currentIndex - 1, 0, list);
      }, 0);
    },
    moveNext() {
      let list = this.blocks.filter((item, index) => {
        if (index !== this.currentIndex) return item;
      });
      let item = this.blocks[this.currentIndex];
      list.splice(this.currentIndex + 1, 0, item);
      this.blocks = list;
      console.log(this.blocks);
    },
    del() {
      this.blocks.splice(this.currentIndex, 1);
      this.cancelSelect();
    },
    async submitData(val) {
      let page = {};
      page.title = this.page.title;
      page.desc = this.page.desc;
      page.data = JSON.stringify({
        page: this.page,
        blocks: this.blocks
      });
      console.log(page);
      let data = await request.postJson("/api/pages/update/" + this.id, page);
    }
  }
};
</script>

<style scoped>
.no-btn {
  border-radius: 0;
  height: 50px;
}
.header {
  background: #409eff;
  color: #fff;
  text-align: left;
  line-height: 50px;
  padding: 0;
}
.header .item {
  float: left;
  min-width: 30px;
  text-align: center;
}
.wrap {
  flex-wrap: wrap;
}
#main {
  margin: 20px 0 20px 20px;
  width: 750px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.8);
  background: #fff;
  flex: 0 0 750px;
}
#editor {
  position: fixed;
  margin: 20px 10px;
  width: 400px;
}
#setting {
  margin: 20px 10px 0 3px;
  width: 26px;
}
#setting .content {
  position: relative;
  left: 0;
}
#setting .item {
  background: #fff;
  border-bottom: solid 1px #ccc;
}
#setting button {
  padding: 8px 6px;
  border: none;
  border-radius: 0;
}
</style>



