<template>
  <div class="img" :style="{backgroundColor:blockData.backgroundColor,height:blockData.height+'px'}">
    <img style="height:100%"  :src="blockData.backgroundImage" />
    <div class="mark" @click="checkDrag">
      <vue-draggable-resizable :active="selected&&index==currentRange" :cancel="false" v-for="(item,index) of blockData.ranges" :key="index" :w="item.w" :h="item.h" :x="item.x" :y="item.y" class="range" :parent="true" v-on:dragstop="onDrag" v-on:resizestop="onResize"
        @activated="onActivate(index)" @deactivated="deActivate(index)">
        <i @click="delRange(index,item)" v-if="selected&&index==currentRange" class="el-icon-error handle-close"></i>
      </vue-draggable-resizable>
    </div>
  </div>
</template>

<script>
import VueDraggableResizable from "@dal/vue-draggable-resizable";
import { rangeFactory } from "../common/defaultData";
import imgEvent from "../components/eventSolt";

export default {
  components: {
    VueDraggableResizable
  },
  props: ["block", "selected", "id"],
  data() {
    return {
      blockData: this.block,
      currentRange: -1
    };
  },
  mounted() {
    //添加一个新的热区
    imgEvent.$on("addRange", (eid) => {
      if(eid!==this.id)return;
      this.blockData.ranges.push(rangeFactory());
      this.submitData();
    });
    //更新基本信息
    imgEvent.$on("updateBase", (eid,val) => {
      if(eid!==this.id)return;
      for (const key in val) {
        if (val.hasOwnProperty(key) && key !== "ranges") {
          const element = val[key];
          this.blockData[key] = element;
        }
      }
      this.submitData();
    });
    //更新当前选中对象
    imgEvent.$on("updateRange", (eid,val) => {
      console.log(eid)
      if(eid!==this.id)return;
      this.blockData.ranges = this.blockData.ranges.filter((item, index) => {
        if (index == this.currentRange) {
          item = val;
        }
        return item;
      });
      this.submitData();
    });
  },
  methods: {
    /**
       * 拖动结束之后改变值并上传
       */
    onDrag(x, y) {
      this.blockData.ranges = this.blockData.ranges.filter((range, index) => {
        if (this.currentRange == index) {
          range.x = x;
          range.y = y;
        }
        return range;
      });
      this.submitData();
    },
    /**
       * 改变大小之后改变值并上传
       */
    onResize(x, y, width, height) {
      this.blockData.ranges = this.blockData.ranges.filter((range, index) => {
        if (this.currentRange == index) {
          range.x = x;
          range.y = y;
          range.w = width;
          range.h = height;
        }
        return range;
      });
      this.submitData();
    },
    /**
       * 点击设置当前的值
       */
    onActivate(index) {
      this.currentRange = index;
      imgEvent.imgData = this.blockData.ranges[index];
      imgEvent.$emit("current", this.id,this.blockData.ranges[index]);
    },
    deActivate(index) {},
    /**
       * 点击别的地方取消点击效果
       */
    checkDrag(e) {
      let target = e.target || e.srcElemt;
      if (!/draggable|handle/.test(target.className)) {
        this.currentRange = -1;
        imgEvent.$emit("current", this.id,null);
      }
    },
    //删除
    delRange(id, data) {
      let list = [];
      for (let index = 0; index < this.blockData.ranges.length; index++) {
        const item = this.blockData.ranges[index];
        if (item !== data) {
          list.push(item);
        }
      }
      this.blockData.ranges = [];
      setTimeout(() => {
        this.blockData.ranges = list;
        this.submitData();
      }, 0);
    },
    submitData() {
      this.$emit("change", this.blockData);
    }
  }
};
</script>

<style scoped>
.img {
  width: 100%;
  display: flex;
  position: relative;
}
.mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.range {
  background: rgba(64, 158, 255, 0.5);
}
.range.active {
  background: rgba(235, 158, 5, 0.5);
}
</style>



