<template>
    <div class="pro_list" :style="{backgroundColor:block.backgroundColor}">
        <div v-for="(item,index) in blockData" :key="index" :style="{width:block.listChannel+'0%'}">
            <img :src="item.image" />
        </div>
    </div>
</template>

<script>
    import proEvent from "../components/eventSolt";
    import VueDraggableResizable from "@dal/vue-draggable-resizable";
    export default {
        components: {
            VueDraggableResizable
        },
        props: ["block", "id"],
        data() {
            return {
                blockData: this.block.list,
                blockStyle: this.block.styles,
                currentItem: null
            };
        },
        mounted() {
            console.log(this.blockData);
        },
        methods: {}
    };
</script>

<style scoped>
    .pro_list {
        display: flex;
        flex-wrap: wrap;
    }
</style>



