<template>
    <div @click.stop>
        <div class="bg">
            <div class="title">
                商品列表设置
            </div>
            <el-tabs class="bg" value="base" @tab-click="handleClick">
                <el-tab-pane name="base">
                    <span slot="label" class="tab-title">基本样式</span>
                    <el-form ref="form" class="form-table">
                        <el-form-item label="背景色">
                            <el-color-picker @change="updateColor" v-model="block.backgroundColor"></el-color-picker>
                        </el-form-item>
                        <el-form-item label="显示列数">
                            <el-radio-group @change="submitData" v-model="block.listChannel" size="small">
                                <el-radio-button label="10">单列</el-radio-button>
                                <el-radio-button label="5">双列</el-radio-button>
                            </el-radio-group>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane name="items">
                    <span slot="label" class="tab-title">商品样式</span>
                    <el-form ref="form" class="form-table">
                        <el-form-item label="添加热区">
                            <el-button @click="addRange" type="primary" size="mini">添加</el-button>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane name="list">
                    <span slot="label" class="tab-title">商品列表</span>
                    <el-form ref="form" class="form-table">
                        <div class="row">
                            <el-input v-model="skus" placeholder="请输入sku。多个使用英文,分割"></el-input>
                            <el-button @click="addSku" type="primary" size="mini">添加</el-button>
                        </div>
                        <div class="row">
                            <div class="item" v-for="(item,index) in blockData" :key="index">
                                {{item.goodsShowName}}
                            </div>
                        </div>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
        </div>
        <!--[提示]-->
    </div>
</template>

<script>
    import request from "../common/request";
    import proEvent from "../components/eventSolt";
    export default {
        props: ["block", "id"],
        data() {
            return {
                blockData: this.block.list,
                blockStyle: this.block.styles,
                currentItem: null,
                skus: "",
            };
        },
        methods: {
            handleClick() {},
            async addSku() {
                if (!this.skus) return this.$message.error('请填写sku');
                let data = await request.getJson('/api/product/' + this.skus);
                console.log(data)
                this.blockData.push(...data);
                this.submitData();
            },
            addRange() {},
            updateColor(val) {
                console.log(val)
            },
            submitData() {
                this.$emit("change");
            }
        }
    };
</script>

<style scoped>
    .bg {
        background: #fff;
        margin-bottom: 10px;
        box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.8);
    }
    .title {
        padding: 5px;
        border-bottom: solid 1px #ccc;
    }
    .tab-title {
        padding: 0 5px;
        font-size: 12px;
    }
    .el-form-item{
        margin-bottom: 1px;
    }
    .form-table {
        padding: 5px 10px;
    }
    .row {
        display: flex;
    }
</style>



