<template>
  <el-container>
    <!--[头部]-->
    <el-header class="header" height="50px">
      <el-button class="no-btn" @click="leftmenu" type="primary" icon="el-icon-menu"></el-button>
      达令™ TPM
      <div style="float:right">
        <el-dropdown @command="selectAdd">
          <el-button type="primary" class="no-btn">
            创建新页面<i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="add_m">移动端页面</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dropdown v-if="isLogin" @command="selectAdd">
          <el-button type="primary" class="no-btn">
            {{nickname}}
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="quit">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-button v-if="!isLogin" @click="login" type="primary" class="no-btn">登录</el-button>
      </div>
    </el-header>
    <el-container>
      <!--[左侧]-->
      <el-aside width="150px" class="menu">
        <el-menu default-active="1" class="" :collapse="isCollapse">
          <el-menu-item index="1">
            <i class="el-icon-mobile-phone"></i>
            <span slot="title">移动端</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <!--[中间]-->
        <el-main class="list">
          <el-table :data="list" style="width: 100%" current-row-key="id">
            <el-table-column align="center" prop="id" label="ID" width="50">
            </el-table-column>
            <el-table-column align="center" prop="title" label="页面标题" width="200">
            </el-table-column>
            <el-table-column align="center" label="线上地址" width="400">
              <template slot-scope="scope">
                            <span v-if="scope.row.status==0">未部署</span>
                            <a v-else :href="scope.row.url" target="_blank">{{scope.row.url}}</a>
</template>
              </el-table-column>
              <el-table-column
                align="center"
                prop="update_time"
                label="修改时间"
                width="200">
              </el-table-column>
    
            <el-table-column label="操作" align="center" v-if="isLogin">
<template slot-scope="scope">
  <el-button-group>
    <el-button type="primary" class="mini-btn" size="mini" @click="handleEdit(scope.$index, scope.row)">
      编辑</el-button>
    <!-- <el-button type="success" class="mini-btn" size="mini" @click="handlePreview(scope.$index, scope.row)">
      预览</el-button> -->
    <el-button class="mini-btn" type="warning" size="mini" @click="handleDraff(scope.$index, scope.row)">
      部署</el-button>
  </el-button-group>
  <el-button class="mini-btn" size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)" icon="el-icon-close"></el-button>
</template>
            </el-table-column>
          </el-table>
          <div style="text-align:center">
        <el-pagination class="" @current-change="goNext"
                :current-page.sync="currentPage1" :page-size="20"
          layout="prev, pager, next"
          :total="count">
        </el-pagination>
          </div>
        </el-main>
      </el-container>
    </el-container>
    <el-dialog
    title=""
      :visible.sync="showLogin"
      width="30%">
  <el-input
    placeholder="请输入用户名"
    prefix-icon="el-icon-warning"
    v-model="username">
  </el-input>
  <p></p>
  <el-input
    placeholder="请输入密码"
    type="password"
    prefix-icon="el-icon-warning"
    v-model="pwd">
  </el-input>
  <span slot="footer" class="dialog-footer">
    <el-button @click="showLogin = false">取 消</el-button>
    <el-button type="primary" @click="goLogin">确 定</el-button>
  </span>
</el-dialog>
  </el-container>
</template>

<script>
  import request from '../common/request';
  export default {
    name: "index",
    data() {
      return {
        isCollapse: false,
        list: [],
        count: 0,
        currentPage1: 1,
        isLogin: false,
        showLogin: false,
        username: "",
        pwd: "",
        nickname: "",
      };
    },
    async mounted() {
      let data = await request.getJson("/api/auth");
      //登录
      if (data.code == 1) {
        this.isLogin = true;
        this.nickname = data.data;
      }
      this.refresh()
    },
    methods: {
      async refresh() {
        let res = await request.getJson("/api/pages/list");
        res.data.rows.map(row=>{
          row.update_time=this.formatTime(row.update_time);
          return row;
        });
        this.count = res.data.count;
        this.list = res.data.rows;
      },
      formatTime(time){
        var obj=new Date(time);
        var str="yyyy-MM-dd";
        str=str.replace("yyyy",obj.getFullYear());
        str=str.replace("MM",obj.getMonth()+1);
        str=str.replace("dd",obj.getDate());
        return str;
      },
      selectAdd(val) {
        console.log(val)
        if (this[val]) {
          this[val]();
        }
      },
      async goNext(cur){
        this.currentPage1=cur;
        let res = await request.getJson("/api/pages/list",{limit:cur});
        res.data.rows.map(row=>{
          row.update_time=this.formatTime(row.update_time);
          return row;
        });
        this.count = res.data.count;
        this.list = res.data.rows;
      },
      async add_m() {
        let data = await request.getJson('/api/pages/create');
        if (data.code == 1) {
          this.$router.push("/item/" + data.data);
        }
      },
      leftmenu() {
        this.isCollapse = !this.isCollapse;
      },
      quit() {
        this.isLogin = false;
        document.cookie = "token=";
      },
      login() {
        this.showLogin = true;
      },
      async goLogin() {
        let data = await request.postJson('/api/auth/login', {
          username: this.username,
          pwd: this.pwd
        })
        if (data.code == 1) {
          this.isLogin = true;
          this.nickname = data.data;
          this.refresh()
        }
        this.showLogin = false;
      },
      async handleDraff(index, row) {
        try {
          await this.$confirm("是否部署该页面?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          });
          let res = await request.postJson('/api/pages/draff/' + row.id);
          if (res.code !== 1) return;
          this.list = this.list.filter((item, i) => {
            if (i == index) {
              item.status = 1;
            }
            return item;
          });
        } catch (e) {}
      },
      handleEdit(index, row) {
        this.$router.push("/item/" + row.id);
      },
      async handleDelete(index, row) {
        try {
          let res = await this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          });
          //删除
          let jsonres = await request.getJson('/api/pages/del', {
            id: row.id
          });
          if (jsonres.code != 1) return;
          this.list = this.list.filter((item, i) => {
            if (i != index) return item;
          });
        } catch (e) {}
      }
    }
  };
</script>

<style scoped>
  .header {
    background: #409eff;
    color: #fff;
    text-align: left;
    line-height: 50px;
  }
  .list {
    padding: 20px;
    height: 600px;
  }
  .no-btn {
    border-radius: 0;
    height: 50px;
  }
  .mini-btn {
    padding: 5px 10px;
  }
</style>



